/*
 
 @param imgO
 @param url
 @param xx
 @param yY
 @param w
 @param h
 */
function addImg(url,x,y,w,h,stage,layer,fn,id){
    var imgO=new Image();
    imgO.onload = function() {
        var imgObj = new Konva.Image({
            x: x ,
            y: y ,
            image: imgO,
            id: id ? id : '',
            width: w,
            height: h,
            preventDefault: false
        });
        layer.add(imgObj);
        stage.add(layer);
        fn && fn(imgObj,layer);
    };
    imgO.src = url;
};
/*
 sprite
 @param imgO
 @param url
 @param xx
 @param yY
 @param anis
 */
function addSprite(url,x,y,anis,ani,stage,layer,fn,id,rate){
    var imgO=new Image();
    var spriteObj=null;
    imgO.onload = function() {
        spriteObj = new Konva.Sprite({
            x: x ,
            y: y ,
            image: imgO,
            id: id ? id : '',
            animation: ani,
            animations:anis,
            frameRate: rate ? rate : 2,
            frameIndex: 0
        });
        layer.add(spriteObj);
        stage.add(layer);
        spriteObj.start();
        fn && fn(spriteObj,layer);
    };
    imgO.src = url;
};
/*
 
 @param xx
 @param yx
 @param r
 @param color
 @param stage
 @param layer
 @param dotArr
 */
function drawDot(x,y,r,color,stage,layer,dotArr){
    var circle = new Konva.Circle({
        x: x,
        y: y,
        radius: r,
        fill: color
    });
    layer.add(circle);
    stage.add(layer);
    dotArr && dotArr.push(circle);
};
/*
 ,
 @param arr
 @param w
 @param color
 @param stage
 @param layer
 @param lineArr
 */
function drawLine(arr,w,color,stage,layer,lineArr){
    var line = new Konva.Line({
        points: arr,
        stroke: color,
        strokeWidth: w
    });
    layer.add(line);
    stage.add(layer);
    lineArr && lineArr.push(line);
    return line;
};
/*
 
 @param str
 @param txX
 @param tyY
 @param font{},
 @param stage
 @param layer
 @param lineArr
 */
function drawText(str,tx,ty,font,stage,layer,lineArr){
    var simpleText = new Konva.Text({
        x: tx,
        y: ty,
        text: str,
        fontSize: font.size,
        fontFamily: font.family,
        fill: font.color
    });
    layer.add(simpleText);
    stage.add(layer);
    lineArr && lineArr.push(simpleText);
    return simpleText;
};
module.exports={
    addImg:addImg,
    addSprite:addSprite,
    drawDot:drawDot,
    drawLine:drawLine,
    drawText:drawText
};


