//
function rotateAn(obj,angle,fn){
    var radius=obj.cr;
    var midX=obj.cx;
    var midY=obj.cy;
    var iconX,iconY;
    var rotateO=obj.circleObj ? obj.circleObj : null;
    if(angle<90){
        iconX=midX+Math.cos(angle/180*Math.PI)*radius;
        iconY=midY+Math.sin(angle/180*Math.PI)*radius;
    }else if(angle==90){
        iconX=midX;
        iconY=midY+radius;
    }else if(angle>90 && angle<180){
        iconX=midX-Math.sin((angle-90)/180*Math.PI)*radius;
        iconY=midY+Math.cos((angle-90)/180*Math.PI)*radius;
    }else if(angle==180){
        iconX=midX-radius;
        iconY=midY;
    }else if(angle>180 && angle<270){
        iconX=midX-Math.cos((angle-180)/180*Math.PI)*radius;
        iconY=midY-Math.sin((angle-180)/180*Math.PI)*radius;
    }else if(angle==270){
        iconX=midX;
        iconY=midY-radius;
    }else if(angle>270 && angle<360){
        iconX=midX+Math.sin((angle-270)/180*Math.PI)*radius;
        iconY=midY-Math.cos((angle-270)/180*Math.PI)*radius;
    }else if(angle==360){
        iconX=midX+radius;
        iconY=midY;
    };
    if(rotateO){
        rotateO.setX(parseInt(iconX+obj.difx));
        rotateO.setY(parseInt(iconY+obj.dify));
    };
    fn && fn(obj,iconX,iconY);
    return {
        x:iconX,
        y:iconY
    };
};
//html
function replaceHtml(obj){
    obj.replaceWith(obj.attr('data-html'));
}


